import React, { useCallback, useState, useEffect } from "react";

import { TextField } from "@material-ui/core";
import { Autocomplete } from "@material-ui/lab";

import useFirestore from "../hooks/useFirestore";

function GroupAutoComplete({ onChange, value, ...otherProps }) {
  const firestore = useFirestore();

  const [options, setOptions] = useState([]);
  const [optionNames, setOptionNames] = useState({});

  useEffect(() => {
    const fn = async () => {
      const { docs: groupsRef } = await firestore.collection("groups").get();
      let optionNamesObject = {};
      const groupData = await groupsRef.map((groupRef) => {
        optionNamesObject = {
          ...optionNamesObject,
          [groupRef.id]: groupRef.data().name,
        };
        return groupRef.id;
      });

      setOptionNames(optionNamesObject);
      setOptions(groupData);
    };

    fn();
  }, [firestore]);

  const getOptionLabel = useCallback(
    (option) => {
      return optionNames ? optionNames[option] || "" : "";
    },
    [optionNames],
  );

  const renderInput = useCallback(
    (params) => {
      const inputProps = {
        ...params.inputProps,
        value: optionNames ? optionNames[value] || "" : "",
      };

      return (
        <TextField
          {...params}
          {...{ inputProps }}
          label="Groups"
          variant="outlined"
          fullWidth
        />
      );
    },
    [optionNames, value],
  );

  return (
    <Autocomplete
      id="id-groups-autocomplete"
      autoComplete
      autoHighlight
      {...{
        options,
        getOptionLabel,
        renderInput,
        onChange,
        value,
        ...otherProps,
      }}
    />
  );
}

export default GroupAutoComplete;
