import React, { useEffect } from "react";
import _ from "lodash";
import * as api from "../service/api";
import useFirebase from "../hooks/useFirebase";
import { NotificationManager } from "react-notifications";

var UserStateContext = React.createContext();
var UserDispatchContext = React.createContext();

function userReducer(state, action) {
  switch (action.type) {
    case "LOGIN_SUCCESS":
      return { ...state, isAuthenticated: true };
    case "LOGIN_FAILURE":
      return { ...state, isAuthenticated: false };
    case "SIGN_OUT_SUCCESS":
      return { ...state, isAuthenticated: false };
    default: {
      throw new Error(`Unhandled action type: ${action.type}`);
    }
  }
}

function UserProvider({ children }) {
  const firebase = useFirebase();

  var [state, dispatch] = React.useReducer(userReducer, {
    isAuthenticated: -1,
  });

  useEffect(() => {
    return firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        dispatch({ type: "LOGIN_SUCCESS" });
        // userStorage.set(user && pick(user, "uid", "email", "photoURL"));
        localStorage.setItem(
          "user",
          JSON.stringify(_.pick(user, "uid", "email")),
        );
      } else {
        dispatch({ type: "LOGIN_FAILURE" });
      }
    });
  }, [firebase]);

  return (
    <UserStateContext.Provider value={state}>
      <UserDispatchContext.Provider value={dispatch}>
        {children}
      </UserDispatchContext.Provider>
    </UserStateContext.Provider>
  );
}

function useUserState() {
  var context = React.useContext(UserStateContext);
  if (context === undefined) {
    throw new Error("useUserState must be used within a UserProvider");
  }
  return context;
}

function useUserDispatch() {
  var context = React.useContext(UserDispatchContext);
  if (context === undefined) {
    throw new Error("useUserDispatch must be used within a UserProvider");
  }
  return context;
}

export {
  UserProvider,
  useUserState,
  useUserDispatch,
  loginUser,
  registerUser,
  signOut,
};

// ###########################################################
async function registerUser(
  firebase,
  firestore,
  dispatch,
  firstname,
  lastname,
  email,
  password,
  history,
  setIsLoading,
  setError,
) {
  setError(false);
  setIsLoading(true);

  try {
    const {
      user: { uid },
    } = await firebase.auth().createUserWithEmailAndPassword(email, password);

    await firestore.collection("users").doc(uid).set({
      email,
      firstname,
      lastname,
      emailReminder: true,
      emailStatus: true,
      notificationReminder: true,
      notificationStatus: true,
    });

    dispatch({ type: "LOGIN_SUCCESS" });
    setError(null);
    setIsLoading(false);
    history.push("/profile");
  } catch (e) {
    console.log("regeister failed", e.message);
    dispatch({ type: "LOGIN_FAILURE" });
    setError(true);
    setIsLoading(false);

    NotificationManager.error(e.message || "Something went wrong :(");
  }
}

async function loginUser(
  firebase,
  dispatch,
  email,
  password,
  history,
  setIsLoading,
  setError,
) {
  setError(false);
  setIsLoading(true);
  try {
    const aa = await firebase
      .auth()
      .signInWithEmailAndPassword(email, password);
    console.log("login", aa);
    dispatch({ type: "LOGIN_SUCCESS" });
    setError(null);
    setIsLoading(false);
    history.push("/profile");
  } catch (e) {
    dispatch({ type: "LOGIN_FAILURE" });
    setError(true);
    setIsLoading(false);
    NotificationManager.error(e.message || "Something went wrong :(");
  }
}

async function signOut(firebase, dispatch, history) {
  firebase.auth().signOut();
  dispatch({ type: "SIGN_OUT_SUCCESS" });
  history.push("/");
}
