import React, { useCallback, useState, useEffect } from "react";
import {
  makeStyles,
  Paper,
  Button,
  Grid,
  FormControlLabel,
  Checkbox,
  TextField,
  Fade,
  LinearProgress,
  Typography,
  Divider,
  Slider,
} from "@material-ui/core";
// components
import PageTitle from "../components/PageTitle/PageTitle";
import useFirestore from "../hooks/useFirestore";
import useUser from "../hooks/useUser";
import { KeyboardDatePicker } from "@material-ui/pickers";
import { NotificationManager } from "react-notifications";
import { getFormattedDate } from "../helpers/date";

const journalObject = [
  {
    key: "energy",
    title: "Factor 1 Engergy",
    min: 0,
    max: 10,
    marks: [
      { value: 0, label: "0" },
      { value: 10, label: "10" },
    ],
  },
  {
    key: "calm",
    title: "Factor 2 Calm",
    min: 0,
    max: 10,
    marks: [
      { value: 0, label: "Calm 0" },
      { value: 10, label: "Axious 10" },
    ],
  },
  {
    key: "mood",
    title: "Factor 3 Mood",
    min: 0,
    max: 10,
    marks: [
      { value: 0, label: "manic 0" },
      { value: 10, label: "10 depressed" },
    ],
  },
  {
    key: "physicalStress1",
    title: "Factor 4 Physical Stress Response",
    min: 0,
    max: 200,
    marks: [
      { value: 0, label: "systolic 0" },
      { value: 200, label: "200" },
    ],
  },
  {
    key: "physicalStress2",
    title: "",
    min: 0,
    max: 120,
    marks: [
      { value: 0, label: "diastolic 0" },
      { value: 120, label: "120" },
    ],
  },
  {
    key: "chemicalStress",
    title: "Factor 5 Chemical Stress Response",
    min: 0,
    max: 20,
    marks: [
      { value: 0, label: "cortisol 0" },
      { value: 20, label: "20" },
    ],
  },
  {
    key: "happiness",
    title: "Happiness",
    min: 0,
    max: 10,
    marks: [
      { value: 0, label: "0" },
      { value: 10, label: "10" },
    ],
  },
  {
    key: "hours",
    title: "Hours Worked",
    min: 0,
    max: 24,
    marks: [
      { value: 0, label: "0" },
      { value: 24, label: "24" },
    ],
  },
];

const journalInitData = {
  energy: 0,
  calm: [0, 10],
  mood: [0, 10],
  physicalStress1: 0,
  physicalStress2: 0,
  chemicalStress: 0,
  happiness: 0,
  hours: 0,
};

const useStyles = makeStyles((theme) => ({
  titleContainer: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
}));

function SetMyJournal() {
  const classes = useStyles();
  const firestore = useFirestore();

  const [user] = useUser();

  const [isLoading, setLoading] = useState(false);
  const [date, setDate] = useState(Date.now());
  const [journalData, setJournalData] = useState(journalInitData);

  useEffect(() => {
    const fn = async () => {
      setLoading(true);
      const doc = await firestore
        .collection("journals")
        .where("userId", "==", user.id)
        .where("date", "==", getFormattedDate(date))
        .get();

      if (!doc.empty) {
        setJournalData({ ...doc.docs[0].data(), id: doc.docs[0].id });
      }
      else {
        setJournalData(journalInitData);
      }
      setLoading(false);
    };

    if (date && user) fn();
  }, [date, firestore, user]);

  const onSave = useCallback(async () => {
    setLoading(true);
    try {
      const stringedDate = getFormattedDate(date);

      if (journalData.id) {
        await firestore
          .collection("journals")
          .doc(journalData.id)
          .update({
            ...journalData,
          });
      } else {
        await firestore.collection("journals").add({
          ...journalData,
          userId: user.id,
          date: stringedDate,
        });
      }

      NotificationManager.success("Your data has been received.");
    } catch (e) {
      NotificationManager.warning("Failed");
    }

    setLoading(false);
  }, [date, firestore, journalData, user]);

  const handleSliderChange = useCallback(
    (key) => (event, newValue) => {
      setJournalData({ ...journalData, [key]: newValue });
    },
    [journalData],
  );

  const getAriaValueText = useCallback((value) => {
    return `${value}`;
  }, []);

  return (
    <>
      <Fade in={isLoading || !journalData}>
        <LinearProgress variant="query" />
      </Fade>
      <div className={classes.titleContainer}>
        <PageTitle title="Set My Journals" />
      </div>
      {journalData && (
        <Paper style={{ padding: 40, maxWidth: 600 }}>
          <Grid container spacing={3}>
            <Grid item container spacing={2} justify="center">
              <Grid item>
                <KeyboardDatePicker
                  id="id-date-date"
                  label="Select Date"
                  format="MM/dd/yyyy"
                  disableFuture
                  value={date}
                  onChange={(date) => {
                    console.log("jj", date);
                    setDate(date);
                  }}
                />
              </Grid>
            </Grid>
            <Grid
              item
              container
              spacing={3}
              justify="center"
              style={{ margin: "0 10px" }}
            >
              {journalObject.map((data, index) => {
                const { key, title, min, max, marks } = data;
                return (
                  <Grid
                    item
                    container
                    spacing={1}
                    direction="column"
                    key={`journal-slider-${index}`}
                  >
                    <Grid item>
                      <Typography
                        id={`slider-${key}`}
                        gutterBottom
                        style={{
                          fontStyle: "italic",
                          textTransform: "uppercase",
                        }}
                      >
                        {title}
                      </Typography>
                    </Grid>
                    <Grid item>
                      <Slider
                        aria-labelledby={`slider-${key}`}
                        value={journalData[key]}
                        onChange={handleSliderChange(key)}
                        valueLabelDisplay="auto"
                        {...{ min, max, marks, getAriaValueText }}
                      />
                    </Grid>
                  </Grid>
                );
              })}
            </Grid>
            <Grid item container spacing={2} justify="center">
              <Grid item>
                <Button variant="contained" color="primary" onClick={onSave}>
                  Save
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      )}
    </>
  );
}

export default SetMyJournal;
