import axios from "axios";
import jwt_decode from "jwt-decode";

const StatusCode = {
  SUCCESS: 200,
  REDIRECTION: 300,
  CLIENT_ERROR: 400,
  SERVER_ERROR: 500,
};

const apiBaseUrl = "http://localhost:5007";

export const setAuthToken = token => {
  if (token) {
    axios.defaults.headers.common["Authorization"] = token;
  } else {
    delete axios.defaults.headers.common["Authorization"];
  }
};

setAuthToken(localStorage.getItem("jwtToken"));

export const register = async userData => {
  try {
    const res = await axios.request({
      url: "/api/users/register",
      baseURL: apiBaseUrl,
      method: "post",
      data: userData,
    });
    if (res.status === StatusCode.SUCCESS) {
      const { token } = res.data;
      setAuthToken(token);
      localStorage.setItem("jwtToken", token);
      const decoded = jwt_decode(token);
      return { status: true, result: decoded };
    }
  } catch (err) {
    if (err.response) {
      const { response } = err;
      return { status: false, message: response.data.message };
    }
  }
  return { status: false };
};

export const login = async userData => {
  try {
    const res = await axios.request({
      url: "/api/users/login",
      baseURL: apiBaseUrl,
      method: "post",
      data: userData,
    });

    const { token } = res.data;
    setAuthToken(token);
    localStorage.setItem("jwtToken", token);
    const decoded = jwt_decode(token);
    return { status: true, result: decoded };
  } catch (err) {
    console.log("login err", err.response);
    if (err.response) {
      const { response } = err;
      return {
        status: false,
        title: response.data.title,
        message: response.data.message,
      };
    }
  }
  return { status: false };
};

export const loginWithToken = async token => {
  try {
    setAuthToken(token);

    const res = await axios.request({
      url: "/api/users/login-with-token",
      baseURL: apiBaseUrl,
      method: "post",
    });

    return { status: true };
  } catch (err) {
    if (err.response) {
      const { response } = err;
      return {
        status: false,
        // title: response.data.title,
        // message: response.data.message,
      };
    }
  }
  setAuthToken(false);
  return { status: false };
};

export const logout = async () => {
  try {
    const res = await axios.request({
      url: "/api/users/logout",
      baseURL: apiBaseUrl,
      method: "post",
    });

    setAuthToken(false);
    return { status: true };
  } catch (err) {
    if (err.response) {
      const { response } = err;
      return {
        status: false,
        title: response.data.title,
        message: response.data.message,
      };
    }
  }
  setAuthToken(false);
  return { status: false };
};

