import React, { useCallback, useState, useEffect } from "react";
import {
  makeStyles,
  Paper,
  Button,
  Grid,
  FormControlLabel,
  Checkbox,
  TextField,
  Fade,
  LinearProgress,
  Typography,
  Divider,
} from "@material-ui/core";
// components
import PageTitle from "../components/PageTitle/PageTitle";
import useFirestore from "../hooks/useFirestore";
import useUser from "../hooks/useUser";
import GroupAutoComplete from "../components/GroupAutoComplete";

const preferenceObject = [
  { label: "email reminder", key: "emailReminder" },
  { label: "email status", key: "emailStatus" },
  { label: "notification reminder", key: "notificationReminder" },
  { label: "notification status", key: "notificationStatus" },
];

const useStyles = makeStyles((theme) => ({
  titleContainer: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  grow: {
    flexGrow: 1,
  },
  checkbox: {
    maxWidth: 280,
    display: "flex",
    "& .MuiFormControlLabel-label": {
      flexGrow: 1,
    },
  },
}));

function ProfilePage() {
  const classes = useStyles();
  const [user, updateUser] = useUser();
  const [userData, setUserData] = useState();
  const [isLoading, setLoading] = useState(false);

  useEffect(() => {
    setUserData(user);
  }, [user]);

  const handleUserData = useCallback(
    (fieldName) => (e) => {
      setUserData({ ...userData, [fieldName]: e.target.value });
    },
    [userData],
  );

  const handleGroupSelect = useCallback(
    (event, newValue) => {
      console.log("dsf", newValue)
      setUserData({
        ...userData,
        groupId: newValue,
      });
    },
    [userData],
  );

  const handleCheckbox = useCallback(
    (key) => (e) => {
      setUserData({ ...userData, [key]: e.target.checked });
    },
    [userData],
  );

  const onUpdate = useCallback(async () => {
    setLoading(true);
    await updateUser({ ...userData });
    setLoading(false);
  }, [updateUser, userData]);

  console.log("user123", userData);
  return (
    <>
      <div className={classes.titleContainer}>
        <PageTitle title="Profile" />
      </div>
      <Fade in={isLoading}>
        <LinearProgress variant="query" />
      </Fade>
      <Paper style={{ padding: 20 }}>
        {userData && (
          <Grid container direction="column" spacing={4}>
            <Grid item container spacing={2}>
              <Grid item xs={12} sm={3}>
                <TextField
                  id="first-name"
                  label="First name"
                  value={userData.firstname || ""}
                  onChange={handleUserData("firstname")}
                  fullWidth
                />
              </Grid>
              <Grid item xs={12} sm={3}>
                <TextField
                  fullWidth
                  id="last-name"
                  label="Last name"
                  value={userData.lastname || ""}
                  onChange={handleUserData("lastname")}
                />
              </Grid>
            </Grid>

            <Grid item container spacing={2}>
              <Grid item xs={12} sm={6}>
                {console.log("123123", userData.groupId)}
                <GroupAutoComplete
                  value={userData.groupId}
                  onChange={handleGroupSelect}
                />
              </Grid>
            </Grid>

            <Grid item container style={{ marginTop: 20 }} spacing={1}>
              <Typography variant="h4" weight="medium">
                Communication Preferences
              </Typography>
            </Grid>
            <Grid item container spacing={2} direction="column">
              {preferenceObject.map(({ label, key }) => {
                console.log("321", label, key, userData[key]);
                return (
                  <Grid key={`id-checkbox-${key}`} item xs={12} sm={4}>
                    <FormControlLabel
                      className={classes.checkbox}
                      control={
                        <Checkbox
                          checked={userData[key]}
                          onChange={handleCheckbox(key)}
                        />
                      }
                      labelPlacement="start"
                      label={label}
                    />
                  </Grid>
                );
              })}
            </Grid>
            <Grid item>
              <Divider />
            </Grid>
            <Grid item container spacing={2} justify="center">
              <Button color="primary" variant="contained" onClick={onUpdate}>
                Update
              </Button>
            </Grid>
          </Grid>
        )}
      </Paper>
    </>
  );
}

export default ProfilePage;
