import React, { useState, useCallback, useEffect } from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Divider,
  TextField,
  Button,
  Fade,
  LinearProgress,
  makeStyles,
  Grid,
} from "@material-ui/core";

import * as api from "../service/api";

const fields = [
  {
    key: "fullname",
    label: "Full name",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "textfield",
  },
  {
    key: "email",
    label: "Email",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "textfield",
  },
  {
    key: "phonenumber",
    label: "Phone number",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "textfield",
  },
  {
    key: "country",
    label: "Country",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "autoComplete",
  },
  {
    key: "city",
    label: "City",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "textfield",
  },
  {
    key: "address",
    label: "Address",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "textfield",
  },
  {
    key: "zipcode",
    label: "Zip Code",
    layout: {
      xs: 12,
      md: 6,
    },
    inputType: "textfield",
  },
];

const useStyles = makeStyles({
  dialogContent: {
  },
});

function ProfileDialog(props) {
  const { open = true, popDialog } = props;
  const classes = useStyles();

  const [loading, setLoading] = useState(true);
  const [profile, setProfile] = useState({});

  useEffect(() => {
    const fn = async () => {
      setLoading(true);
      const { profile } = await api.getProfile();
      setProfile(profile);
      setLoading(false);
    };

    fn();
  }, []);

  const onSave = useCallback(async () => {
      setLoading(true);
      const { profile: data } = await api.updateProfile(profile);
      setLoading(false);
      if(data) {
          popDialog();
      }
  }, [popDialog, profile]);

  const handleInput = fieldName => e => {
    setProfile({ ...profile, [fieldName]: e.target.value });
  };

  const handleCountrySelect = useCallback(
    (event, newValue) => {
        setProfile({ ...profile, country: newValue });
    },
    [profile],
  );

  return (
    <Dialog scroll="body" maxWidth="md" {...{ open }}>
      <Fade in={loading} timeout={800}>
        <LinearProgress variant="query" />
      </Fade>
      <DialogTitle>Profile</DialogTitle>
      <Divider light />
      <DialogContent className={classes.dialogContent}>
        <Grid container spacing={2}>
          {fields.map((field, index) => {
            const { layout, label, key, inputType } = field;
            if (inputType === "autoComplete") {
              return (
                <Grid item {...layout}>
                 
                </Grid>
              );
            }

            return (
              <Grid item {...layout}>
                <TextField
                  label={label}
                  fullWidth
                  value={profile[key] || ""}
                  onChange={handleInput(key)}
                />
              </Grid>
            );
          })}
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={popDialog}>Cancel</Button>
        <Button onClick={onSave} color="primary">
          Save
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default ProfileDialog;
